import React, { Component } from 'react'
import PropTypes from 'prop-types'

const postStyle = {
    fontSize: 19,
    padding: 5,
    borderBottom: '3px dotted #eee'
}

export default class Posts extends Component {
  render() {
    return (
      <ul>
        {this.props.posts.map((post, i) => <li key={i} style={postStyle}>{post.title}</li>)}
      </ul>
    )
  }
}

Posts.propTypes = {
  posts: PropTypes.array.isRequired
}


