const divStyle = {
    padding: 25,
    borderTop: '1px solid #ddd',
    marginTop: 50,    
  	textAlign: 'center',
}

export default class Footer extends Component {
  render() {
    return (
    	<div style={divStyle}>
          <p>Info in this site is an auto generated search through the web from (www.reddit.com) site.</p>
          <p>therefor information may get late news</p>
	      <p>Tecnology used in building this site: create-react-app - redux react-redux react-router-dom react-router-redux@next redux-thunk history babel/polyfill cross-fetch redux-logger -- GitHub</p>
	      <p>Developed By Ehab Ashour - 2018</p>
	    </div>
    )
  }
}


